<template>
	<div>
		<publicHead :title="msg"></publicHead>
		<div class="detail">
			<h5 v-text="details[cur].title"></h5>
			<p v-text="details[cur].desc"></p>
		</div>
	</div>
</template>

<script>
	import publicHead from '../../common/publicHeader'
	export default {
		data() {
			return {
				iconRight: require('../../../assets/common/arrow_right.png'),
				msg: '',
				cur: this.$route.params.id || 0,
				details: [
					{
						title: '1. APP?',
						desc: ',APP. , "+", "", ,"","", . , "", . "", .'
					},
					{
						title: '2. ?',
						desc: '"", /,. , . .'
					},
					{
						title: '3. ?',
						desc: '"", , , . : "", : "". . , .'
					},
					{
						title: '4. ?',
						desc: '"""""", "".'
					},
					{
						title: '5. ?',
						desc: '"", . WIFI, : (1) ; (2) ; (3) ; (4) , .'
					},
					{
						title: '6. ?',
						desc: '"""","""",(), .'
					}
				]
			}
		},
		components: {
			publicHead
		},
		mounted(){

		}
	}
</script>

<style scoped>
	.detail {
		margin-top: .8rem;
	}
	.detail h5 {
		font-size: .9rem;
		text-align: left;
		padding: .8rem .8rem .4rem .8rem;
	}
	.detail p {
		width: 96%;
		font-size: .8rem;
		line-height: 1.3rem;
		text-align: left;
		margin: auto;
	}
</style>


