<template>
	<div>
		<publicHead :title="msg"></publicHead>
		<div class="recommend-bg" :style="'backgroundImage: url('+ recommendBg +')'">

			<div class="rule">
				<h5 class="title"><img :src="iconHeader" alt=""><span></span></h5>
				<div class="rule-cont">
					<img class="icon-footer" :src="iconFooter" alt="">
					<ul>
						<li><img :src="first" alt=""></li>
						<li><img :src="second" alt=""></li>
						<li><img :src="third" alt=""><span>500</span>25000</li>
						<li><img :src="fourth" alt=""><span></span></li>
						<li><img :src="fifth" alt="">10</li>
						<li><img :src="sixth" alt="">1231</li>
						<li><img :src="seventh" alt=""></li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHead from '../../common/publicHeader'
	export default {
		data() {
			return {
				msg: '',
				recommendBg: require('../../../assets/user/recommend/recommend_bg.png'),
				giftPic: require('../../../assets/user/recommend/charge.jpg'),
				recommendNumBg: require('../../../assets/user/recommend/recommend_num.png'),
				iconHeader: require('../../../assets/user/recommend/rule_header.png'),
				iconFooter: require('../../../assets/user/recommend/rule_footer.png'),
				first: require('../../../assets/user/recommend/first.png'),
				second: require('../../../assets/user/recommend/second.png'),
				third: require('../../../assets/user/recommend/third.png'),
				fourth: require('../../../assets/user/recommend/fourth.png'),
				fifth: require('../../../assets/user/recommend/fifth.png'),
				sixth: require('../../../assets/user/recommend/sixth.png'),
				seventh: require('../../../assets/user/recommend/seventh.png')
			}
		},
		components: {
			publicHead
		}
	}
</script>

<style scoped>
	.recommend-bg {
		width: 100%;
		height: 100vh;
		position: absolute;
		left: 0;
		top: 0;
		background-size: 100%;
		background-repeat: no-repeat;
	}
	.rule {
		width: 83%;
		border-radius: 10px;
		margin: auto;
		padding-top: 1.6rem;
		padding-bottom: 5rem;
		position: relative;
		top: 4.2rem;
	}
	.title {
		position: relative;
	}
	.title span {
		display: block;
		width: 100%;
		font-size: .9rem;
		position: absolute;
		left: 0;
		top: 1.2rem;
	}
	.title img {
		width: 100%;
	}
	.rule-cont {
		box-shadow: 1px 1px 10px #ccc;
		border-bottom-left-radius: 12px;
		border-bottom-right-radius: 12px;
		background-color: #fff;
		padding-bottom: 2rem;
	}
	.rule-cont ul {
		width: 85%;
		margin: auto;
	}
	.rule-cont li {
		font-size: .9rem;
		text-align: justify;
		line-height: 1.4rem;
		margin-bottom: .8rem;
		word-wrap: break-word;
	}
	.rule-cont li span {
		color: #ff9c00;
    	font-size: 1.1rem;
	}
	.rule-cont img {
		width: 1.3rem;
		margin-right: .4rem;
		position: relative;
		top: 0;
		float: left;
	}
	.rule-cont .icon-footer {
		width: 100%;
		position: relative;
		top: -.3rem;
	}
</style>


