<template>
	<div class="sign-in-rule">
		<publicHead :title="msg"></publicHead>
		<div class="rule white">
			<h5></h5>
			<p>1 5</p>
			<p>2 6</p>
			<p>3 7</p>
			<p>4 8</p>
			<p>5 9</p>
			<p>6 10</p>
			<p>7 11</p>
			<p>7,</p>
			<p>1 5</p>
			<p>,,</p>
		</div>
		<div class="single white">
			<h5>1.?</h5>
			<p></p>
		</div>
		<div class="single white">
			<h5>2.?</h5>
			<p>1.</p>
			<p>2.</p>
		</div>
		<div class="single white">
			<h5>3.?</h5>
			<p></p>
		</div>
		<div class="single white">
			<h5>4.?</h5>
			<p></p>
		</div>
		<div class="single white">
			<h5>5.?</h5>
			<p>1231</p>
		</div>
	</div>
</template>

<script>
	import publicHead from '../../common/publicHeader'
	export default {
		data() {
			return {
				msg: ''
			}
		},
		components: {
			publicHead
		}
	}
</script>

<style scoped>
	.sign-in-rule {
		color: #000;
		text-align: left;
		background-color: #f2f2f2;
	}
	.rule {
		font-size: .8rem;
		line-height: 1.8rem;
		padding: .8rem;
	}
	.rule h5,.single h5 {
		font-size: 1rem;
	}
	.single {
		font-size: .8rem;
		line-height: 1.4rem;
		padding: 1.2rem .8rem;
		margin-top: .7rem;
	}
	.single h5 {
		margin-bottom: .2rem;
	}
</style>


